@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@100;300;400;700&display=swap);
.NavbarContainer {
  background: #191622;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
}

.HomeButton {
  color: #67e480;
  transition: 0.3s;
}
.HomeButton:hover {
  filter: brightness(70%);
}

a {
  text-decoration: none;

  transition: 0.3s;
}

.LinksContainer {
  width: 400px;
  height: 100%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;

  font: 700 1.1rem "Heebo";
}

.LinksContainer a {
  color: #988bc7;
}

.NavbarContainer a:first-child {
  align-self: center;
  margin-left: 30px;
  font-family: "Heebo", sans-serif;
}

.ProfileContainer {
  height: 80vh;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.AboutMe {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.AboutMe * {
  color: #ffffff;
}

.AboutMe button {
  width: 250px;
  height: 70px;
  border-radius: 8px;
  border: solid 2px #988bc7;
  background: none;
  font: 700 1.2rem Heebo;
  color: #988bc7;

  transition: 0.8s;
}

.AboutMe button:hover {
  background: #988bc7;
  color: #191622;
  cursor: pointer;
}

.AboutMe h2 {
  margin-bottom: 30px;
  font: 700 2rem "Heebo";
  letter-spacing: 3px;
}

.AboutMe p {
  margin-bottom: 30px;
  font: 500 1.1rem "Heebo";
}

.Photo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 100%;
}

.Photo div {
  width: 400px;
  height: 400px;

  border-radius: 50%;
  background-image: url("https://avatars.githubusercontent.com/u/67125215?s=400&u=554b73aeced7fd3787d632c6b94a083fa34eeb1b&v=4");
}

html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.HomeContainer {
  background: #191622;
}

